import closeFill from "@iconify/icons-eva/close-fill";
import { Icon } from "@iconify/react";
import { LoadingButton } from "@mui/lab";
import {
  Alert,
  Card,
  IconButton,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { Form, FormikProvider, useFormik } from "formik";
import { useSnackbar } from "notistack";
import * as Yup from "yup";
import useResetPassword from "../../hooks/user/useResetPassword";

interface InitialValues {
  password: string;
  confirmPassword: string;
  afterSubmitMessage?: string;
}

interface SetPasswordProps {
  goToNextStep: () => void;
  currentUsername: string;
}

export default function SetPassword({
  goToNextStep,
  currentUsername,
}: SetPasswordProps): JSX.Element {
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const resetPassword = useResetPassword();

  const setPasswordSchema = Yup.object().shape({
    password: Yup.string().min(6, "6").required(""),
    confirmPassword: Yup.string()
      .required("")
      .oneOf([Yup.ref("password"), null], ""),
  });

  const formik = useFormik<InitialValues>({
    initialValues: {
      password: "",
      confirmPassword: "",
    },
    validationSchema: setPasswordSchema,
    onSubmit: async (values, { setErrors, setSubmitting, resetForm }) => {
      try {
        const message = await resetPassword(currentUsername, values.password);
        if (message !== null) {
          resetForm();
          setSubmitting(false);
          setErrors({
            afterSubmitMessage: message,
          });
        } else {
          enqueueSnackbar("", {
            variant: "success",
            action: key => (
              <IconButton size="small" onClick={() => closeSnackbar(key)}>
                <Icon icon={closeFill} />
              </IconButton>
            ),
          });
          setSubmitting(false);
          goToNextStep();
        }
      } catch (error) {
        console.log(" " + error);
      }
    },
  });

  return (
    <Card sx={{ padding: 3 }}>
      <Typography variant="subtitle1" sx={{ mb: 5 }}>
        {currentUsername}
      </Typography>
      <FormikProvider value={formik}>
        <Form autoComplete="off" onSubmit={formik.handleSubmit} noValidate>
          <Stack spacing={3} direction="column">
            {formik.errors.afterSubmitMessage && (
              <Alert severity="error">{formik.errors.afterSubmitMessage}</Alert>
            )}

            <TextField
              {...formik.getFieldProps("password")}
              fullWidth
              autoComplete="on"
              type="password"
              label=""
              error={Boolean(formik.touched.password && formik.errors.password)}
              helperText={formik.touched.password && formik.errors.password}
            />

            <TextField
              {...formik.getFieldProps("confirmPassword")}
              fullWidth
              autoComplete="on"
              type="password"
              label=""
              error={Boolean(
                formik.touched.confirmPassword && formik.errors.confirmPassword
              )}
              helperText={
                formik.touched.confirmPassword && formik.errors.confirmPassword
              }
            />

            <LoadingButton
              fullWidth
              size="large"
              type="submit"
              variant="contained"
              loading={formik.isSubmitting}
            >
              
            </LoadingButton>
          </Stack>
        </Form>
      </FormikProvider>
    </Card>
  );
}
